<template>
  <div class="main">
    <ul>
      <li class="blogItem" v-for="item in noteList">
        <h2 class="blogTitle">{{item.caption}}</h2>
        <span class="blogUpTime">{{item.updatetime}}</span>
        <p class="blogIntro">{{item.intro}}</p>
        <!--<a class="blogDetail" @click="$goRoute('noteItem',{noteData:item})">阅读全文»</a>-->
        <a class="blogDetail" target="_blank" :href="item.url">阅读全文»</a>
      </li>
    </ul>
    <!--<a @click="$goRoute('/noteItem')">详情</a>-->
  </div>
</template>

<script>
  export default {
    name: 'home',
    data() {
      return {
        home: '这是主页'
      }
    },
    computed: {
      noteList() {
        return this.$store.state.noteList.notes
      }
    },
    components: {},
    mounted: function () {
      // console.log(11, new Date);
      // let _that = this;
//      _that.noteList = _that.$store.state.noteList.notes;
    },

  }
</script>

<style lang="scss">
  .main{
    .blogItem{
      position: relative;
      margin: 30px 0;
      .blogTitle{
        font-size: 32px;
        margin-bottom: 25px;
        /*font-weight: bold;*/
      }
      .blogUpTime{
        position: absolute;
        top: 10px;
        right: 0;
        color: #555;
      }
      .blogIntro{
        margin-bottom: 16px;
      }
      .blogDetail{
        color: #2479CC;
        cursor: pointer;
      }
    }
  }
  @media screen and (max-width: 768px) {
    .main{
      .blogUpTime{
        display: none;
      }
    }
  }
</style>



